import { _decorator, Color, Component, Label, Node, Sprite } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('NameHP')
export class NameHP extends Component {
    @property(Node) hpBar:Node;
    @property(Label) lblName:Label;

    public set hpPercent(v:number){
        this.hpBar.setScale(v,1);
    }

    public set name(v:string){
        this.lblName.string = v;
    }

    public set color(c:Color){
        this.hpBar.getComponent(Sprite).color = c;
        this.lblName.color = c;
    }
}

