import { Node } from "cc";
import { ITankPlayer } from "../../module_basic/shared/protocols/public/tank/TankTypeDef";

export type TankType = {
    node:Node,
    player:ITankPlayer,
    beAttack:Function;
}

export class TankMgr{
    private static _inst:TankMgr;
    public static get inst():TankMgr{
        if(this._inst == null){
            this._inst = new TankMgr();
        }
        return this._inst;
    }

    private _tankList:TankType[] = [];

    public get tankList():TankType[]{
        return this._tankList;
    }

    public addTank(tank:TankType){
        this._tankList.push(tank);
    }

    public removeTank(tank:TankType){
        this._tankList.splice(this._tankList.indexOf(tank),1);
    }
}