import { _decorator, Component, Node, v3, Vec3, Vec2, v2, Prefab, instantiate, tween, KeyCode } from 'cc';
import { TankGameMgr, TankSkill } from './TankGameMgr';
import { Tank } from './Tank';
import { TankMovement2D } from './TankMovement2D';

const { ccclass, property } = _decorator;

const tempV2 = v2();

@ccclass('TankController')
export class TankController extends Component {
    @property(Prefab)
    bullet: Prefab;

    @property(Node)
    firePoint: Node;

    @property(Node)
    barrel: Node;

    private _movement2d: TankMovement2D;
    private _tank: Tank;

    start() {
        this._tank = this.node.getComponent(Tank);

        tgx.EasyController.on(tgx.EasyControllerEvent.BUTTON, this.onButtonHit, this);

        this._movement2d = this.node.getComponent(TankMovement2D);
    }

    onButtonHit(btnSlot: string) {
        if (TankGameMgr.inst.selfPlayer.hp <= 0) {
            return;
        }
        if (btnSlot == 'btn_skill_1') {
            if (TankGameMgr.inst.castSkill(TankSkill.SKILL_1)) {
                this._tank.shoot();
            }
        }
        if (btnSlot == 'btn_skill_2') {
            if (TankGameMgr.inst.castSkill(TankSkill.SKILL_2)) {
                this._movement2d.tempSpeedUp(2.0, 3000);
            }
        }
    }

    protected lateUpdate(dt: number): void {
        if (this._movement2d?.getRealSpeed() > 0) {
            let pos = this.node.worldPosition;
            let rotation = this.node.eulerAngles;
            TankGameMgr.inst.sendMsg_TankDataChange(pos.x, pos.y, pos.z, rotation.z);
        }
    }

    onDestroy() {
        tgx.EasyController.off(tgx.EasyControllerEvent.BUTTON, this.onButtonHit, this);
    }
}


