import { _decorator, Component, director, Node, Vec3 } from 'cc';
import { TankGameEvent, TankGameMgr } from './TankGameMgr';
import { ITankPlayer } from '../../module_basic/shared/protocols/public/tank/TankTypeDef';
const { ccclass, property } = _decorator;

const tempV3 = new Vec3();

@ccclass('TankMovement2D')
export class TankMovement2D extends tgx.CharacterMovement2D {
    private _tmpSpeedFactor = 1.0;
    private _speedUpEndTime = 0;
    start(): void {
        super.start();
        director.on(TankGameEvent.PLAYER_DIE,this.onEvent_PlayerDie,this);
    }

    onDestroy(): void {
        director.off(TankGameEvent.PLAYER_DIE,this.onEvent_PlayerDie,this);
    }

    onEvent_PlayerDie(player:ITankPlayer){
        //if player dies, stop movement
        //如果玩家死亡，则停止移动
        if(player.playerId == TankGameMgr.inst.selfPlayer.playerId){
            this.onMovementStop();
        }
    }

    public tempSpeedUp(factor:number,time:number){
        this._tmpSpeedFactor = factor;
        this._speedUpEndTime = Date.now() + time;
    }

    onMovement(degree, strengthen){
        if(TankGameMgr.inst.selfPlayer.hp <= 0){
            return;
        }
        super.onMovement(degree,strengthen);
    }

    getRealSpeed(){
        return super.getRealSpeed() * this._tmpSpeedFactor;
    }

    update(deltaTime: number): void {
        super.update(deltaTime);
        if(this.getRealSpeed() > 0){
            this.node.getWorldPosition(tempV3);
            if(tempV3.lengthSqr() > 1500*1500){
                let len = tempV3.length();
                let dx = tempV3.x / len;
                let dy = tempV3.y / len;
                tempV3.x = dx * 1500;
                tempV3.y = dy * 1500;
                this.node.setWorldPosition(tempV3);
            }
        }

        if(this._speedUpEndTime > 0 && Date.now() >= this._speedUpEndTime){
            this._tmpSpeedFactor = 1.0;
            this._speedUpEndTime = 0;
        }
    }
}

