import { director } from "cc";
import { GameMgr, GameMode } from "../../module_basic/scripts/GameMgr";
import { SubGameMgr } from "../../module_basic/scripts/SubGameMgr";
import { UserMgr } from "../../module_basic/scripts/UserMgr";
import { ITankGameData, ITankPlayer } from "../../module_basic/shared/protocols/public/tank/TankTypeDef";
import { MsgGameOverPush } from "../../module_basic/shared/protocols/public/tank/MsgGameOverPush";
import { MsgGameBeginPush } from "../../module_basic/shared/protocols/public/tank/MsgGameBeginPush";
import { MsgPlayerLeavesPush } from "../../module_basic/shared/protocols/public/tank/MsgPlayerLeavesPush";
import { MsgPlayerDataChangedPush } from "../../module_basic/shared/protocols/public/tank/MsgPlayerDataChangedPush";
import { MsgPlayerComesPush } from "../../module_basic/shared/protocols/public/tank/MsgPlayerComesPush";
import { MsgGameDataChangedPush } from "../../module_basic/shared/protocols/public/tank/MsgGameDataChangedPush";
import { MsgGameDataSyncPush } from "../../module_basic/shared/protocols/public/tank/MsgGameDataSyncPush";
import { MsgTankDataChange } from "../../module_basic/shared/protocols/public/tank/MsgTankDataChange";
import { MsgTankShoot } from "../../module_basic/shared/protocols/public/tank/MsgTankShoot";
import { MsgTankAttack } from "../../module_basic/shared/protocols/public/tank/MsgTankAttack";
import { NetGameServer, gameNet } from "../../module_basic/scripts/NetGameServer";
import { RoomMgr } from "../../module_basic/scripts/RoomMgr";
import { SubGameDef } from "../../module_basic/scripts/SubGameDef";
import { SceneDef } from "../../scripts/SceneDef";
import { ModuleDef } from "../../scripts/ModuleDef";

export class TankGameEvent {
    public static ROOM_PUSH = 'TankGameEvent.ROOM_PUSH';
    public static PLAYER_COMES = 'TankGameEvent.PLAYER_COMES';
    public static PLAYER_LEAVES = 'TankGameEvent.PLAYER_LEAVES';
    public static PLAYER_DATA_CHANGED = 'TankGameEvent.PLAYER_DATA_CHANGED';
    public static GAME_BEGIN = 'TankGameEvent.GAME_BEGIN';
    public static GAME_OVER = 'TankGameEvent.GAME_OVER';
    public static TANK_DATA_CHANGED = 'TankGameEvent.TANK_DATA_CHANGED';
    public static TANK_SHOOT = 'TankGameEvent.TANK_SHOOT';
    public static TANK_ATTACK = 'TankGameEvent.TANK_ATTACK';

    public static PLAYER_DIE = 'TankGameEvent.PLAYER_DIE';
    public static PLAYER_REVIVE = 'TankGameEvent.PLAYER_REVIVE';
}

export class TankSkill {
    public static SKILL_1 = 1000;
    public static SKILL_2 = 1001;
    public id: number;
    public cd: number;
}

export class TankSillCastInfo {
    skill: TankSkill;
    lastCastTime: number;

    get isInCD() {
        let cdTime = this.lastCastTime + this.skill.cd - Date.now();
        return cdTime > 0;
    }

    get cdPercent() {
        let cdTime = this.lastCastTime + this.skill.cd - Date.now();
        if (cdTime <= 0) {
            return 0.0;
        }
        return cdTime / this.skill.cd;
    }
}

const skillMap = {};
skillMap[TankSkill.SKILL_1] = { id: TankSkill.SKILL_1, cd: 2000 };
skillMap[TankSkill.SKILL_2] = { id: TankSkill.SKILL_1, cd: 30000 };

export class TankGameMgr extends GameMgr {
    private static _inst: TankGameMgr;
    public static get inst(): TankGameMgr {
        if (!this._inst) {
            this._inst = new TankGameMgr();
        }
        return this._inst;
    }

    constructor(){
        super();
        this._gameType = SubGameDef.TANK;
        this._entryScene = { name: 'lobby_tank', bundle: ModuleDef.TANK };
        this._gameScene =  { name: 'game_tank', bundle: ModuleDef.TANK };
        this.initNetMsgHandlers();
    }

    private _gameData: ITankGameData;
    private _playerMap: { [key: number]: ITankPlayer } = {};
    private _skillCdMap: { [key: number]: TankSillCastInfo } = {};

    private _selfPlayer: ITankPlayer;
    public get selfPlayer(): ITankPlayer {
        return this._selfPlayer;
    }

    public get gameData(): Readonly<ITankGameData> {
        return this._gameData;
    }

    public castSkill(skillId: number) {
        let skill = skillMap[skillId];
        if (!skill) {
            return false;
        }

        let cdInfo = this._skillCdMap[skillId];
        if (!cdInfo) {
            cdInfo = new TankSillCastInfo();
            cdInfo.skill = skill;
            cdInfo.lastCastTime = 0;
            this._skillCdMap[skillId] = cdInfo;
        }
        if (cdInfo.isInCD) {
            return false;
        }

        cdInfo.lastCastTime = Date.now();
        return true;
    }
    public getSkillCd(skillId: number) {
        return this._skillCdMap[skillId];
    }

    public async beforeLoadScene(): Promise<void> {
        await RoomMgr.inst.rpc_JoinGame();
    }

    initNetMsgHandlers() {
        gameNet.listenMsg("tank/GameDataSyncPush", this.onNet_GameDataSyncPush, this);
        gameNet.listenMsg("tank/GameDataChangedPush", this.onNet_GameDataChangedPush, this);

        gameNet.listenMsg("tank/PlayerComesPush", this.onNet_PlayerComesPush, this);
        gameNet.listenMsg("tank/PlayerDataChangedPush", this.onNet_PlayerDataChangedPush, this);
        gameNet.listenMsg("tank/PlayerLeavesPush", this.onNet_PlayerLeavesPush, this);

        gameNet.listenMsg("tank/GameBeginPush", this.onNet_GameBeginPush, this);
        gameNet.listenMsg("tank/GameOverPush", this.onNet_GameOverPush, this);

        gameNet.listenMsg('tank/TankDataChange', this.onNet_TankDataChange, this);
        gameNet.listenMsg('tank/TankShoot', this.onNet_TankShoot, this);
        gameNet.listenMsg('tank/TankAttack', this.onNet_TankAttack, this);
    }

    sendMsg_TankDataChange(x: number, y: number, z: number, rotation: number) {
        gameNet.sendMsg('tank/TankDataChange', { x: x, y: y, z: z, rotation: rotation });
    }

    sendMsg_TankShoot(x: number, y: number, z: number, rotation: number, speed: number, lifeTime: number) {
        gameNet.sendMsg('tank/TankShoot', { x: x, y: y, z: z, rz: rotation, speed: speed, lifeTime: lifeTime });
    }

    sendMsg_TankAttack(attackerId: number, targetId: number, damage: number, attackPoint?: { x: number, y: number, z: number }) {
        gameNet.sendMsg('tank/TankAttack', { attackerId: attackerId, targetId: targetId, damage: damage, attackPoint: attackPoint });
    }

    public reset() {
        super.reset();
    }

    public initAIMode() {
        
    }

    private get selfPlayerIndex(): number {
        return this.getPlayerIndex(UserMgr.inst.uid);
    }

    public get leftUserId(): string {
        if (!this._gameData || this._gameData.players.length == 0) {
            return '';
        }

        if (RoomMgr.inst.isPlayer) {
            return UserMgr.inst.uid;
        }
        else {
            return this._gameData.players[0].uid;
        }
    }

    public get rightUserId(): string {
        if (!this._gameData || this._gameData.players.length == 0) {
            return '';
        }

        if (RoomMgr.inst.isPlayer) {
            if (this._gameData.players[0].uid != UserMgr.inst.uid) {
                return this._gameData.players[0].uid;
            }
            else {
                let p = this._gameData.players[1];
                return p ? p.uid : '';
            }
        }
        else {
            let p = this._gameData.players[1];
            return p ? p.uid : '';
        }
    }

    public getPlayer(playerId: number): ITankPlayer {
        if (!this._gameData || !this._gameData.players) {
            return null;
        }
        for (let i = 0; i < this._gameData.players.length; ++i) {
            let p = this._gameData.players[i];
            if (p.playerId == playerId) {
                return p;
            }
        }
        return null;
    }

    private getPlayerIndex(userId: string): number {
        for (let i = 0; i < this._gameData.players.length; ++i) {
            if (this._gameData.players[i].uid == userId) {
                return i;
            }
        }
        return -1;
    }

    // ============= MESSAGE HANDLER ============
    /**
     * This message will arrive before login result.
     * 这个消息会在登录成功返回之前收到。
    */
    onNet_GameDataSyncPush(msg: MsgGameDataSyncPush) {
        this._gameData = msg.data;
        this._gameData.players.forEach(v => {
            this._playerMap[v.playerId] = v;
            if (v.uid == UserMgr.inst.uid) {
                this._selfPlayer = v;
            }
        });
        director.emit(TankGameEvent.ROOM_PUSH);
    }

    onNet_GameDataChangedPush(msg: MsgGameDataChangedPush) {
        if (!this._gameData) {
            return;
        }
    }

    onNet_PlayerComesPush(msg: MsgPlayerComesPush) {
        this._gameData.players.push(msg.player);
        this._playerMap[msg.player.playerId] = msg.player;
        if (msg.player.uid == UserMgr.inst.uid) {
            this._selfPlayer = msg.player;
        }
        director.emit(TankGameEvent.PLAYER_COMES, msg.player);
    }

    onNet_PlayerDataChangedPush(msg: MsgPlayerDataChangedPush) {
        let p = this.getPlayer(msg.playerId);
        if (!p) {
            return;
        }

        if (msg.hp != undefined) {
            p.hp = msg.hp;
        }

        if (msg.maxHp != undefined) {
            p.maxHp = msg.maxHp;
        }

        if (msg.score != undefined) {
            p.score = msg.score;
        }

        if (msg.reviveTime != undefined) {
            p.reviveTime = msg.reviveTime;
            if (msg.reviveTime > 0) {
                director.emit(TankGameEvent.PLAYER_DIE, p);
            }
            else {
                director.emit(TankGameEvent.PLAYER_REVIVE, p);
            }
        }

        director.emit(TankGameEvent.PLAYER_DATA_CHANGED, msg);
    }

    onNet_PlayerLeavesPush(msg: MsgPlayerLeavesPush) {
        for (let i = 0; i < this._gameData.players.length; ++i) {
            let p = this._gameData.players[i];
            if (p.uid == msg.uid) {
                this._gameData.players.splice(i, 1);
                delete this._playerMap[p.playerId];
                director.emit(TankGameEvent.PLAYER_LEAVES, p);
                break;
            }
        }
    }

    onNet_GameBeginPush(msg: MsgGameBeginPush) {
        if (!this._gameData) {
            return;
        }

        this._gameData.players.forEach(v => {
            //v.color = '';
        });

        director.emit(TankGameEvent.GAME_BEGIN);
    }

    onNet_GameOverPush(msg: MsgGameOverPush) {
        director.emit(TankGameEvent.GAME_OVER, msg);
    }

    onNet_TankDataChange(msg: MsgTankDataChange) {
        let p = this.getPlayer(msg.playerId);
        p.x = msg.x;
        p.y = msg.y;
        p.z = msg.z;
        p.rotation = msg.rotation;
        director.emit(TankGameEvent.TANK_DATA_CHANGED, msg);
    }

    onNet_TankShoot(msg: MsgTankShoot) {
        director.emit(TankGameEvent.TANK_SHOOT, msg);
    }

    onNet_TankAttack(msg: MsgTankAttack) {
        director.emit(TankGameEvent.TANK_ATTACK, msg);
    }
}

SubGameMgr.inst.registerGameMgr(TankGameMgr.inst);