import { _decorator, Component, find, instantiate, Node, Prefab, Quat, v2 } from 'cc';
import { TankGameAudioMgr } from './TankGameAudioMgr';
import { Tank } from './Tank';
import { ITankPlayer } from '../../module_basic/shared/protocols/public/tank/TankTypeDef';
import { TankGameMgr } from './TankGameMgr';
import { TankMgr } from './TankMgr';
const { ccclass, property } = _decorator;

@ccclass('TankBullet')
export class TankBullet extends Component {

    @property
    moveSpeed: number = 400;

    @property
    lifeTime: number = 3000;

    @property(Prefab)
    efx: Prefab;

    /**
     * @en the bullet belongs to which player
     * @zh 子弹所属的玩家 id
    */
    player: ITankPlayer;

    damage: number = 10;

    private _lifeStart = 0;
    private _moveDir = v2();
    public set moveDir(v) {
        this._moveDir.set(v);
    }
    start() {
        this._lifeStart = Date.now();
        this.damage = Math.floor(Math.random() * 15 + 10);
    }

    update(deltaTime: number) {
        if (this._lifeStart + this.lifeTime < Date.now()) {
            this.lifeEnd();
            return;
        }
        let dist = this.moveSpeed * deltaTime;
        let dx = this._moveDir.x * dist;
        let dy = this._moveDir.y * dist;
        let pos = this.node.position;
        this.node.setPosition(pos.x + dx, pos.y + dy, pos.z);
    }

    lifeEnd() {
        let efx = instantiate(this.efx);
        find('SceneRoot2D/ground_effects').addChild(efx);
        efx.setWorldPosition(this.node.worldPosition);
        efx.setWorldRotation(this.node.worldRotation);
        let volume = TankGameAudioMgr.getVolumeByDist(this.node.worldPosition, TankGameMgr.inst.selfPlayer);
        TankGameAudioMgr.playOneShot('sounds/sfx_boom', volume);
        this.node.destroy();
    }

    protected lateUpdate(dt: number): void {
        const tankBodyRadius = 50;
        let tankList = TankMgr.inst.tankList;
        for (let i = 0; i < tankList.length; i++) {
            let tank = tankList[i];
            let bp = this.node.worldPosition;
            let tp = tank.node.worldPosition;
            let dx = bp.x - tp.x;
            let dy = bp.y - tp.y;
            if (dx * dx + dy * dy < tankBodyRadius * tankBodyRadius) {
                if(this.player != tank.player){
                    this.lifeEnd();
                }

                /**
                 * @en if this bullet belongs to other player, then this.player is null, no damage calculation
                 * @zh 如果是其他玩家的子弹，this.player 为 null，不做伤害计算
                */
                if (this.player && this.player.color != tank.player.color) {
                    tank.beAttack(this.player.playerId, this.damage, this.node.worldPosition);
                }
                break;
            }
        }
    }
}