import { _decorator, director, instantiate, Label, Node, tween } from 'cc';
import { GameUILayers } from '../../scripts/GameUILayers';
import { Layout_UITankGameHUD } from './Layout_UITankGameHUD';
import { TankGameEvent, TankGameMgr } from '../scripts/TankGameMgr';
import { ModuleDef } from '../../scripts/ModuleDef';
import { UserMgr } from '../../module_basic/scripts/UserMgr';
import { ITankPlayer } from '../../module_basic/shared/protocols/public/tank/TankTypeDef';
import { NoticeLabel } from './NoticeLabel';
import { MsgTankAttack } from '../../module_basic/shared/protocols/public/tank/MsgTankAttack';
import { RoomMgr } from '../../module_basic/scripts/RoomMgr';
import { LanguageData } from '../../module_basic/scripts/language/LanguageData';

@tgx_class(ModuleDef.TANK)
export class UITankGameHUD extends tgx.UIController {
    constructor() {
        super('ui_tank_game_hud/ui_tank_game_hud', GameUILayers.HUD, Layout_UITankGameHUD);
    }

    private _noticeItemPrefab: Node;
    private _noticeItemPool: NoticeLabel[] = [];
    getNoticeItemFromPool(): NoticeLabel {
        let item = this._noticeItemPool.pop();
        if (!item) {
            let node = instantiate(this._noticeItemPrefab);
            item = node.getComponent(NoticeLabel);
        }
        item.node.parent = this._layout.noticeRoot;
        return item;
    }

    putNoticeItemToPool(item: NoticeLabel) {
        item.node.removeFromParent();
        this._noticeItemPool.push(item);
    }

    protected onCreated(): void {
        let layout = this._layout as Layout_UITankGameHUD;
        this._noticeItemPrefab = layout.noticeRoot.children[0];
        layout.noticeRoot.removeAllChildren();


        this.onButtonEvent(layout.btnExit, () => {
            tgx.UIAlert.show(LanguageData.getLangByID("exit_game"), true).onClick(async (b) => {
                if (b == false){
                    return;
                } 
                RoomMgr.inst.exitRoom();
            });
        });

        layout.lblRoomId.string = RoomMgr.inst.data.displayId;
        this.refreshPlayerNumLabel();

        director.on(TankGameEvent.PLAYER_COMES, this.onEvent_PlyaerComes,this);
        director.on(TankGameEvent.PLAYER_LEAVES,this.onEvent_PlayerLeaves,this);
        director.on(TankGameEvent.TANK_ATTACK, this.onEvent_TankAttack,this);
    }

    protected onDispose(): void {
        director.off(TankGameEvent.PLAYER_COMES, this.onEvent_PlyaerComes,this);
        director.off(TankGameEvent.PLAYER_LEAVES,this.onEvent_PlayerLeaves,this);
        director.off(TankGameEvent.TANK_ATTACK, this.onEvent_TankAttack,this);
    }

    async onEvent_PlyaerComes(p:ITankPlayer){
        this.refreshPlayerNumLabel();
        let info = await UserMgr.inst.rpc_GetUserInfo(p.uid);
        if (info) {
            this.addNotice(info.name! + ' 加入了游戏');
        }
    }

    async onEvent_PlayerLeaves(p:ITankPlayer){
        this.refreshPlayerNumLabel();
        let info = await UserMgr.inst.rpc_GetUserInfo(p.uid);
        if (info) {
            this.addNotice(info.name! + ' 离开了游戏');
        }
    }

    async onEvent_TankAttack(msg:MsgTankAttack){
        let target = TankGameMgr.inst.getPlayer(msg.targetId);

        if (target.hp <= 0 && msg.deltaHp) {
            let attacker = TankGameMgr.inst.getPlayer(msg.attackerId);
            let attackerName = '你';
            if (attacker.playerId != TankGameMgr.inst.selfPlayer?.playerId) {
                attackerName = (await UserMgr.inst.rpc_GetUserInfo(attacker.uid))?.name;
            }

            let targetName = '你';
            if (target.playerId != TankGameMgr.inst.selfPlayer?.playerId) {
                targetName = (await UserMgr.inst.rpc_GetUserInfo(target.uid))?.name;
            }
            this.addNotice(attackerName + ' 击败了 ' + targetName);
        }
    }

    addNotice(msg: string) {
        let notice = this.getNoticeItemFromPool();
        notice.string = msg;
        notice.alpha = 255;
        tween(notice).to(3.0, { alpha: 0 }).call(() => {
            this.putNoticeItemToPool(notice);
        }).start();
    }

    refreshPlayerNumLabel() {
        let layout = this._layout as Layout_UITankGameHUD;
        layout.lblPlayerNum.string = '人数：' + RoomMgr.inst.data.playerNum + '/' + RoomMgr.inst.data.maxPlayerNum;
    }

    protected onUpdate(dt: number): void {
        let selfPlayer = TankGameMgr.inst.selfPlayer;
        if (selfPlayer == null) return;
        let layout = this._layout as Layout_UITankGameHUD;
        layout.lblPos.string = 'x:' + ~~selfPlayer.x + ', y:' + ~~selfPlayer.y;
    }
}