import { _decorator, Component, director, instantiate, Node } from 'cc';
import { GameUILayers } from '../../scripts/GameUILayers';
import { Layout_UITankGameRank } from './Layout_UITankGameRank';
import { TankGameEvent, TankGameMgr } from '../scripts/TankGameMgr';
import { MsgPlayerDataChangedPush } from '../../module_basic/shared/protocols/public/tank/MsgPlayerDataChangedPush';
import { TankGameRankItem } from './TankGameRankItem';
import { UserMgr } from '../../module_basic/scripts/UserMgr';
import { ModuleDef } from '../../scripts/ModuleDef';

@tgx_class(ModuleDef.TANK)
export class UITankGameRank extends tgx.UIController {
    constructor(){
        super('ui_tank_game_rank/ui_tank_game_rank',GameUILayers.HUD,Layout_UITankGameRank);
    }
    
    protected onCreated(): void {
        this.refreshRank();

        director.on(TankGameEvent.PLAYER_COMES,this.refreshRank,this);
        director.on(TankGameEvent.PLAYER_LEAVES,this.refreshRank,this);
        director.on(TankGameEvent.PLAYER_DATA_CHANGED,this.onEvent_PlayerDataChanged,this);
    }

    protected onDispose(): void {
        director.off(TankGameEvent.PLAYER_COMES,this.refreshRank,this);
        director.off(TankGameEvent.PLAYER_LEAVES,this.refreshRank,this);
        director.off(TankGameEvent.PLAYER_DATA_CHANGED,this.onEvent_PlayerDataChanged,this);
    }

    onEvent_PlayerDataChanged(msg:MsgPlayerDataChangedPush){
        if(msg.score != undefined){
            this.refreshRank();
        }
    }

    async refreshRank(){
        let players = TankGameMgr.inst.gameData.players;
        players.sort((a,b)=>{
            return b.score - a.score;
        });

        let layout = this._layout as Layout_UITankGameRank;

        layout.listRoot.children.forEach((node)=>{ 
            node.active = false;
        });

        for(let i = 0; i < 10; ++i){
            let p = players[i];
            if(!p){
                break;
            }
            let node = layout.listRoot.children[i];
            if(!node){
                node = instantiate(layout.listRoot.children[0]);
                layout.listRoot.addChild(node);
            }
            node.active = true;
            let item = node.getComponent(TankGameRankItem);
            let name = (await UserMgr.inst.rpc_GetUserInfo(p.uid)).name;
            item.setInfo(i+1,name,p.score);
        }
    }
}