import { _decorator, Color, Component, instantiate, Node, Prefab, Sprite, tween, v3, Vec2, Vec3 } from 'cc';
import { TankBullet } from './TankBullet';
import { TankGameAudioMgr } from './TankGameAudioMgr';
import { ITankPlayer } from '../../module_basic/shared/protocols/public/tank/TankTypeDef';
import { TankGameMgr } from './TankGameMgr';
import { NameHP } from './NameHP';
import { TankMgr } from './TankMgr';
const { ccclass, property } = _decorator;

const tempV2 = new Vec2();

@ccclass('Tank')
export class Tank extends Component {
    @property(Prefab) bullet: Prefab;

    @property(Node) firePoint: Node;

    @property(Node) barrel: Node;

    @property(Sprite) lid: Sprite

    @property(Node) normalState: Node;
    @property(Node) destroyedState: Node;

    public player: ITankPlayer;

    public nameBar: NameHP;

    start() {
        TankMgr.inst.addTank(this);
    }

    setState(state: 'normal'|'destroyed') {
        if (state == 'normal') {
            this.normalState.active = true;
            this.destroyedState.active = false;

        }
        else if (state == 'destroyed') {
            this.normalState.active = false;
            this.destroyedState.active = true;
        }
    }

    setColor(colorVal: number) {
        let c = new Color();
        Color.fromUint32(c, colorVal | 0xff000000);
        this.lid.color = c;
        this.nameBar.color = c;
    }

    protected onDestroy(): void {
        TankMgr.inst.removeTank(this);

        if (this.nameBar) {
            this.nameBar.node.removeFromParent();
            this.nameBar.node.destroy();
            this.nameBar = null;
        }
    }

    beAttack(attackerId, damage: number, attackPoint?: { x: number, y: number, z: number }) {
        TankGameMgr.inst.sendMsg_TankAttack(attackerId, this.player.playerId, damage, attackPoint);
    }

    shootAnim(cb?: Function) {
        let volume = TankGameAudioMgr.getVolumeByDist(this.node.worldPosition, TankGameMgr.inst.selfPlayer);
        TankGameAudioMgr.playOneShot('sounds/sfx_shoot', volume);

        //animation
        let oldPosX = this.barrel.position.x;
        tween(this.barrel).to(0.05, { position: v3(oldPosX - 20, 0, 0) })
            .then(tween(this.barrel).to(0.1, { position: v3(oldPosX, 0, 0) })).call(() => {
                if (cb) {
                    cb();
                }
            })
            .start();
    }

    fireBullet(pos: { x: number, y: number, z: number }, rotationDegrees: number) {
        let bullet = instantiate(this.bullet);
        this.node.parent.addChild(bullet);
        bullet.setWorldPosition(pos.x, pos.y, pos.z);
        bullet.setRotationFromEuler(0, 0, rotationDegrees);

        let rotationAngle = rotationDegrees / 180 * Math.PI;
        let dir = tempV2.set(Math.cos(rotationAngle), Math.sin(rotationAngle));
        bullet.getComponent(TankBullet).moveDir = dir;
        return bullet;
    }

    private _isFiring: boolean = false;
    shoot() {
        if (this._isFiring) {
            return;
        }

        this._isFiring = true;

        let pos = this.firePoint.worldPosition;
        let bullet = this.fireBullet(pos, this.player.rotation);

        this.shootAnim(() => {
            this._isFiring = false;
        });

        let p = bullet.worldPosition;
        let r = bullet.eulerAngles;

        let b = bullet.getComponent(TankBullet);
        b.player = this.player;

        TankGameMgr.inst.sendMsg_TankShoot(p.x, p.y, p.z, r.z, b.moveSpeed, b.lifeTime);
    }

    protected lateUpdate(dt: number): void {
        if (this.nameBar) {
            let sp = this.node.worldPosition;
            this.nameBar.node.setWorldPosition(sp);
        }
    }
}

