import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('DamageLabel')
export class DamageLabel extends Component {

    private _floatSpeed = 20;
    private _lifeTime = 1.0;
    private _elapsedTime = 0;
    start() {
    }

    update(deltaTime: number) {
        this._elapsedTime += deltaTime;
        let p = this.node.position;
        this.node.setPosition(p.x, p.y + deltaTime * this._floatSpeed, p.z);
        if (this._elapsedTime > this._lifeTime) {
            this.node.destroy();
        }
    }
}

