import { _decorator, Component, KeyCode, Node } from 'cc';
import { GameUILayers } from '../../scripts/GameUILayers';
import { Layout_UITankJoystick } from './Layout_UITankJoystick';
import { TankGameMgr, TankSkill } from '../scripts/TankGameMgr';
import { ModuleDef } from '../../scripts/ModuleDef';

const activeSkillIds = [TankSkill.SKILL_1, TankSkill.SKILL_2];

@tgx_class(ModuleDef.TANK)
export class UITankJoystick extends tgx.UIController {
    constructor(){
        super('ui_tank_joystick/ui_tank_joystick',GameUILayers.JOY_STICK,Layout_UITankJoystick);
    }

    protected onCreated(): void {
        tgx.UIJoystick.inst.cleanKeyMap();
        tgx.UIJoystick.inst.bindKeyToButton(KeyCode.KEY_J, 'btn_skill_1');
        tgx.UIJoystick.inst.bindKeyToButton(KeyCode.KEY_K, 'btn_skill_2');
    }

    protected onUpdate(dt: number): void {
        let layout = this._layout as Layout_UITankJoystick;
        activeSkillIds.forEach((skillId) => {
            let cdPercent = 0;
            let cdInfo = TankGameMgr.inst.getSkillCd(skillId);
            if (cdInfo) {
                cdPercent = cdInfo.cdPercent;
            }
            layout.cdSprites[skillId - TankSkill.SKILL_1].fillRange = cdPercent;
        });
    }
}