import { _decorator, Component, Node, UITransform } from 'cc';
import { SceneUtil } from '../../core_tgx/base/SceneUtils';
import { UserMgr } from '../../module_basic/scripts/UserMgr';
import { UI_SearchingRival } from '../../module_basic/ui_searching_rival/UI_SearchingRival';
import { SceneDef } from '../../scripts/SceneDef';
import { UICreateRoom } from '../../module_basic/ui_create_room/UICreateRoom';
import { UIChat } from '../../module_basic/ui_chat/UIChat';
import { UIAnnouncement } from '../../module_basic/ui_announcement/UIAnnouncement';
import { lobbyNet } from '../../module_basic/scripts/NetGameServer';
import { RoomMgr } from '../../module_basic/scripts/RoomMgr';
import { LanguageData } from '../../module_basic/scripts/language/LanguageData';
const { ccclass, property } = _decorator;

@ccclass('TankLobbyScene')
export class TankLobbyScene extends Component {

    @property(UITransform) announcementPlacer:UITransform;

    async start() {
        if (!UserMgr.inst.uid) {
            tgx.SceneUtil.loadScene(SceneDef.START);
            return;
        }

        tgx.UIMgr.inst.closeAll();

        if(lobbyNet.type != 'http'){
            tgx.UIMgr.inst.showUI(UIChat,null,null,lobbyNet);
        }
        
        tgx.UIMgr.inst.showUI(UIAnnouncement,(ui:UIAnnouncement)=>{
            ui.setPosition(this.announcementPlacer.node.position.x,this.announcementPlacer.node.position.y,0.3,0.05);
        });
    }

    onBtnLobby() {
        SceneUtil.loadScene(SceneDef.LOBBY);
    }

    onBtnQuickPlayClicked() {
        tgx.UIMgr.inst.showUI(UI_SearchingRival, (ui: UI_SearchingRival) => {
            this.scheduleOnce(() => {
                ui.startMatch('tank');
            }, 1.5);
        });
    }
    
    async onBtnExit() {
                tgx.UIAlert.show(LanguageData.getLangByID("exit_game"), true).onClick(async (ok: Boolean) => {
                    if (ok) {
                        await RoomMgr.inst.exitRoom();
                    }
                });
            }

    onBtnTraningModeClicked() {
        tgx.UIAlert.show('开发中，敬请期待！');
        //TankGameMgr.inst.initAIMode();
        //tgxSceneUtil.loadScene(SceneDef.TANK);
    }

    onBtnCreate() {
        tgx.UIMgr.inst.showUI(UICreateRoom);
    }

    update(deltaTime: number) {

    }
}